* {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Exo 2', sans-serif;
}

.hl-container {
    display: block;
    justify-content: center;
    align-items: center;
    padding-top: 100px;
    box-shadow: rgba(116, 115, 115, 0.25) 0px 54px 55px, rgba(100, 99, 99, 0.12) 0px -12px 30px, rgba(90, 89, 89, 0.12) 0px 4px 6px, rgba(112, 112, 112, 0.17) 0px 12px 13px, rgba(117, 117, 117, 0.09) 0px -3px 5px;
    
}

.hl-container h3 {
    font-size: 40px;
    font-family: inherit;
    font-weight: lighter;
}

.hlheader {
    width: 80vw;
    height: 600px;
    background-color: white;
    margin: auto;
    margin-top: 150px;
    
    
}


.hlheader .hlg {
    height: 650px;
    width: 100%;
    border-radius: 10px;
   
    background-image: url('../../static/images/Edit.png');
    background-color: white;
    background-repeat: no-repeat;
    background-size: 100% 700px;

}

.hlheader {
    filter: contrast(110%);

    
}



.hlheader .logo {
    position: absolute;
    right: 0px;
    left: 400px;
    padding-top: 500px;
    display: block;
    width: 75%;

}
   


h3 {
    padding: 30px 30px 30px;
    letter-spacing: 1px;
    color: #363636;
}

h4 {
    padding: 30px 30px 30px;
    letter-spacing: 1px;
    color: #363636;
}




